.pool_creation{
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Helvetica Neue', Arial, sans-serif;
   
}
.pool-creation-form>div{
    /* min-width: 550px; */
    /* width: 100%; */

    font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Helvetica Neue', Arial, sans-serif;
    
 
}
.tr{
    display: flex; 
    justify-content: flex-start;
    align-items: center;
}
.th{
    width:  50%;
    flex-basis: 250px;
    margin-right: 10px;
}
.td{
    width:  100%;
    flex-basis: 240px;
    
}
h2,h3{
    text-align: start;
    margin: 0px 0px 10px 0px;
}

.pool_creation select{
    width: 50rem;
    border: none;
    border-bottom: 2px solid #9ca3af;
    display: block;
    flex: 1;
   
    background-color: transparent;
    padding-top: 0.375rem;
    padding-bottom: 0.375rem;
    padding-left: 0.25rem;
    color: #1f2937;
    outline: none ;
    font-size: 0.875rem;
    line-height: 1.25rem;
    /* box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);  */
}
 
.pool_creation input[type="text"]{
    width: 50rem;
    display: block;
    flex: 1;
    
    background-color: white;
    background: transparent;
    padding: 0.375rem 0 0.375rem 0.25rem; 
    color: #1f2937;
   
    font-size: 0.875rem; 
    line-height: 1.5rem;
    /* box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);  */
    border-bottom: 2px solid #9ca3af; 
    
    

  }
.pool_creation input[type="number"]{
    width:50rem;
    display: block;
    flex: 1;
  
    background-color: white;
    background: transparent;
    padding: 0.375rem 0 0.375rem 0.25rem; 
    color: #1f2937;
   
    font-size: 0.875rem; 
    line-height: 1.5rem; 
    /* box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);  */
    border-bottom: 2px solid #9ca3af; 
}
.pool_creation input[type="password"]{
    width: 50rem;
    display: block;
    flex: 1;
   
    background-color: white;
    background: transparent;
    padding: 0.375rem 0 0.375rem 0.25rem; 
    color: #1f2937; 
   
    font-size: 0.875rem; 
    line-height: 1.5rem; 
    /* box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);  */
    border-bottom: 2px solid #9ca3af; 
}
.loading_editpool{
    width: 100px;
    height: 100px;
    
}