.form-body-smtp{
    min-height: 90vh;
    width: 95%;
    border-radius: 15px;
    margin: 0 auto;
 
    background-color: white;
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Helvetica Neue', Arial, sans-serif;
   
    
  }
.smtp-body{
    margin: auto;
    height: 70vh;
    width: 70vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items:center ;
}
.smtp-body form{
    margin-top: 2%;
    width: 40%;
    height: 70vh;
    padding: 20px;
    box-shadow: 0px 0px 5px 1px rgba(55, 37, 221, 0.16);
    border-radius: 15px;
}
.navbar{
    position: inherit;
    display: flex;
    align-items: center;
    /* justify-content: center; */
    margin-bottom: 10px;
    width: 100%;
    border-radius: 5px;
    background-color: rgb(79, 70, 229);
    height: 60px;
    color: white;  
}
.navbar h3{
    font-size: 20px;
    font-weight: bold;
    font-family:'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Helvetica Neue', Arial, sans-serif ;
}
.smtp-body form button{
    width: 100%;
    margin-top: 20px;
    background-color: rgba(55, 37, 221, 0.86);
    text-transform: capitalize;
    color: white;
}
.smtp-body form button :hover{
    background-color: rgba(55, 37, 221, 0.86);
    cursor: pointer;
    font-size: 20px

    
}
.navbar{
    width: 100%;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    margin: 0 auto;
    display: flex;
    justify-content: flex-start;
    

}
.back-button{
    font-size: 25px;
    margin-right: 40%;
    margin-left: 20px;

}
.back-button:hover{
    text-decoration: none;
    color:white;

}

.toggle{
    
    margin-right: 50%;
    margin-left: 20px;
    padding: 10px 5px;
    font-size: 25px;
}
.footer {
    display: flex;
    justify-content:space-between; 
    align-items: center;
    gap: 250px;
    margin-top: 20px;

}

.submit_btn, .test-email-btn {
    font-size: 14px;
    font-weight: 500;
    padding: 8px 16px;
    border-radius: 4px;
    width: 100px;

}

.submit_btn {
    background-color: #1e2a3b;
    color: white;

}

.submit_btn:disabled {
    background-color: #b0b0b0;
}

.footer.test-email-btn:hover{
    background-color: rgba(55, 37, 221, 0.86);
    cursor: pointer;
    font-size: 20px
}

.test-email-btn:disabled {
    background-color: #b0b0b0;
}

@media screen  and (min-width: 768px) and (max-width:1787px){
    .form-body-smtp{
        width: 80%;
       min-height: 100vh;

    }
    .smtp-body form{
        width: 50%;
        height: 100vh;
        margin: auto;
        margin-top: 15%;
        padding: 20px;
       
     
    }
    .navbar h3{
        font-size: 15px;
    }
    .back-button{
        font-size: 20px;
        margin-right: 30%;
    }
    .toggle{
        margin-right: 40%;
        font-size: 15px;
        
    }
    .toggle .FormControlLabel{
        font-size: 15px;
        border: 2px solid red;
    }
    .footer {
        gap: 100px;
    }
    .footer.test-email-btn:hover{
        font-size: 15px;
    }
    .footer.test-email-btn{
        font-size: 15px;
    }
    .submit_btn, .test-email-btn {
        font-size: 12px;
        
        width: auto;
    }
    .footer.test-email-btn:hover{
        font-size: 15px;
    }
    .test-email-btn:disabled {
        font-size: small;
        background-color: #b0b0b0;
    }
    .submit_btn:disabled {
        font-size: small;
        background-color: #b0b0b0;
    }
    
}