/* src/ReportList.css */
.table_container {
   
    width: 95%;
    height: 90vh;
    background-color: white; 
    padding: 20px; 
    border-radius: 8px; 
    display: flex;
    text-align: center;
    flex-direction: column;
    margin: auto;   
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Helvetica Neue', Arial, sans-serif;
}

.report_container {
    position: inherit;
    width: 100%;
    display: flex;
    flex-direction: row; 
    justify-content: flex-end;
}
.report-list {
    position: inherit;
    width: 100%;
    display: flex;
    flex-direction: column; 
}

.report-button {
    background-color: white; 
    color:#3725dddc; 
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    font-weight: 500;
    padding: 5px 10px;
    transition: background-color 0.3s ease;
} 
.report-button:hover {
    background-color: #3725dddc; 
    color:white ;
}
.report-list h1 {
    font-size: 25px;
    color: #333;
    /* margin-left: 50%;  */
    margin: auto;
    display: flex; 
    align-items: center;
    justify-content:center ;
}
.table_container table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px; 
    text-align: center;
    font-size: 15px; 
}

.report-list table tbody tr {
    transition: background-color 0.3s; 
}
.report-list table tbody tr:hover {
    background-color: rgb(189, 187, 187,0.4); 
}
.table_container th{
    border-bottom: 1px solid #dad6d6;
    padding: 12px;
    text-align: center;
    margin-top: 5px;
    
}
.table_container td{
    border-bottom: 1px solid #dad6d6;
    padding: 12px;
    text-align: center;
    margin-top: 5px;
}
.table_container th {
    background-color: #3725dddc;
    color: white;
}
button {
    margin: 0 5px;
    cursor: pointer;
    border: none;
    background: none;
    font-size: 16px;
}
.edit-button i {
    color: blue; 
    padding-right: 20px;
}
.delete-button i {
    color: red; 
}
.pagination {
    position: absolute;
    display: flex;
    justify-content: center;
    margin-top: 20px;
    left: 45%;
    bottom: 50px;
}
.pagination button {
    margin: 0 5px;
    padding: 5px 15px;
    border-radius: 5px;
    background-color: #ffffff;
    cursor: pointer;
}
.pagination button.active {
    background-color: #4a38f0dc;
    color: white;
}
.pagination button:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}
.pagination button:hover:not(:disabled) {
    background-color: #635ca5dc;
  
}
.add-report-container {
    position: absolute;
    display: flex;
    justify-content: flex-end; 
    bottom: 40px;
    right:60px;
   
}
.add-report-button {
    padding: 10px 20px; 
    background-color: #1f1299dc; 
    color: white; 
    border: none; 
    border-radius: 5px; 
    cursor: pointer; 
   
}
.add-report-button:hover {
    background-color: #635ca5dc; 
}
report-details {
    margin-top: 20px;
    padding: 10px;
    border: 1px solid #ccc;
    background-color: #f9f9f9;
}

.tooltip {
    position: relative;
    display: inline-block;
    cursor: pointer;
    border-radius: 50%;
    background-color: white;
    width: 35px;
    height: 35px;
    color: black;
    border: 1px solid grey;
    text-align: center;
}
.tooltip:hover{
    color: white;
    background-color:  #4a38f0dc;
}
.tooltiptext {
    visibility: hidden;
    text-align: center;
    color: black;
    border-radius: 5px;
    padding: 8px;
    position: absolute;
    z-index: 1;
    top: 70%; 
    left: 50%;
    margin-left: -70px;
    opacity: 0; 
    transition: opacity 0.3s; 
}
.tooltip:hover .tooltiptext {
    visibility: visible; 
    opacity: 1; 
}
.circle{
    margin-top: 5px;
}
.user_tooltip {
    position: relative;
    display: inline-block;
    cursor: pointer;
    border-radius: 50%;
    background-color: white;
    width: 35px;
    height: 35px;
    color: black;
    border: 1px solid grey;
    text-align: center;
}
.user_tooltip:hover{
    color: white;
    background-color:  #4a38f0dc;
}
.user_tooltiptext {
    visibility: hidden;
    text-align: center;
    color: black;
    border-radius: 5px;
    padding: 8px;
    position: absolute;
    z-index: 1;
    top: 70%; 
    left: 50%;
    margin-left: -70px; 
    opacity: 0; 
    transition: opacity 0.3s; 
}
.user_tooltip:hover .user_tooltiptext {
    visibility: visible; 
    opacity: 1; 
}
.user_circle{
    margin-top: 5px;
}
.accordion-row {
    background-color: #f9f9f9;
}
.accordion-details {
    padding: 10px;
    border-top: 1px solid #ccc;
}
.accordion-details div {
    display: flex;
    justify-content: center;
    width: 100%;
    margin: auto;
    
}
.accordion-details div strong {
    display: inline-block; 
    margin-right: 5px;  
}
.accordion-details div span {
    display: inline-block;
    max-width: 300px;
    text-overflow: ellipsis; 
    white-space: nowrap; 
    overflow: hidden; 
}
.tableRow tr {
    cursor: pointer;
}
.tableRow tr:hover {
    background-color: #f1f1f1;
}
.status-icon {
    font-size: 1.3em;
}
.success {
    color: green;
}
.pending {
    color: orange;
}
.failure {
    color: red;
}
.default {
    color: #ccc;
}